import React from 'react';


class information extends React.Component {
    constructor(props) {
        super(props);
        this.state = { file:'', imagePreviewUrl:'https://upload.cc/i1/2020/03/19/9wyBXq.jpg'};
      }

    handleImageChange = (e) => {
        let reader = new FileReader();
        let file = e.target.files[0];

        reader.onloadend = () => {
            this.setState({
            file: file,
            imagePreviewUrl: reader.result
            });
        }
        reader.readAsDataURL(file);
        e.target.value = null;
    }

  render() {
    const {imagePreviewUrl} = this.state;
    return (
      <div className="content">
        <img style={{width:'150px',height:'150px'}}src={imagePreviewUrl} alt="" />
        <div className="upload-btn-wrapper">
			<button className="_button">Upload Photo</button>
			<input type="file" onChange={this.handleImageChange} accept="image/*" />
	    </div>
        <input type="text" placeholder="name"  />
        <input type="text" placeholder="input your emotion here" />
        <div className="upload-btn-wrapper">
			<button className="_button">Update</button>
	    </div>
        <div className="upload-btn-wrapper">
			<button className="_button">Logout</button>
	    </div>
      </div>
    );
  }
}

export default information;
